import React, {useEffect, useRef} from 'react';
import './fonts/fonts.sass'
import './App.sass';
import {Header} from "./Components/Header/Header";
import {AboutMe} from "./Components/AboutMe/AboutMe";
import {Relationships} from "./Components/Relationships/Relationships";
import {GeneralRequirements} from "./Components/General requirements/GeneralRequirements";
import {Users} from "./Components/Users/Users";
import {SignUp} from "./Components/Sign up/SignUp";
import {Footer} from "./Components/Fotter/Footer";







function App({data}) {
    let [users, setUsers] = React.useState([])
    let [count, setCount] = React.useState(12)
    let [vissible, setVissible] = React.useState(true)

    const aboutMe = React.createRef()
    const relationships = React.createRef()
    const requirements = React.createRef()
    const scrollUsers = React.createRef()
    const signUp = React.createRef()

    const refs = [aboutMe, relationships, requirements, scrollUsers, signUp]


    let scrollToBlock = (block) => {

        let timer = setInterval(function() {


            if(window.pageYOffset <= block.current.offsetTop){
                window.scrollBy(0, (function(){

                    if(window.pageYOffset + 100 > block.current.offsetTop){

                        return clearInterval(timer)
                    }

                    return 100
                })())
            }else if(window.pageYOffset > block.current.offsetTop) {
                window.scrollBy(0, -100)
            }else{
                clearInterval(timer)
            }
        }, 20)



    }



    let getUsers = async() => {
        let url = 'https://frontend-test-assignment-api.abz.agency/api/v1/users?page=1&count=6';
        let response = await fetch(url);
        let commits = await response.json();

        let freeArr = []

        for(let i = 0; i < commits.users.length; i ++){
            let{photo, name, position, email, phone} = commits.users[i]
            let freeObj = {photo, name, position, email, phone}
            freeArr.push(freeObj)

        }
        setUsers(users = freeArr)
    }

    useEffect(()=>{
        getUsers()
    },[])

    let moreUsers = () => {
        (async () => {

            let url = `https://frontend-test-assignment-api.abz.agency/api/v1/users?page=1&count=${count}`;
            let response = await fetch(url);
            const commits = await response.json();


            let freeArr = []

            for(let i = 0; i < commits.users.length; i ++){
                let{photo, name, position, email, phone} = commits.users[i]
                let freeObj = {photo, name, position, email, phone}
                freeArr.push(freeObj)

            }
            setUsers(users = freeArr)

            setCount(count+=6)
            if(!commits.links.next_url){
                setVissible(false)
            }
        })()
    }


    return (
      <div className={'main_container'}>
        <Header data={data.nav}  scrollTo = {scrollToBlock} ref={refs}/>
        <AboutMe
            data={data.about_me}
            ref={aboutMe}
            scroll={scrollToBlock}
            elemScroll={refs[4]}
        />
        <Relationships
            data={data.relationships}
            ref={relationships}
            scroll={scrollToBlock}
            elemScroll={refs[4]}
        />
        <GeneralRequirements data={data.general_requirements}  ref={requirements}/>
        <Users data={data.users} users={users} onClick = {moreUsers} vissible={vissible}  ref={scrollUsers}/>
        <SignUp data={data.sign_Up} getNewUser={getUsers} forwardRef={signUp}/>
        <Footer  data={data}/>

    </div>
  );
}

export default App;
