import React from "react";
import {Nav} from "./Nav/Nav";
import {Links} from "./Links/Links";
import {Icons} from "./Icons/Icons";
import './Footer.sass'

export const Footer = ({data}) => {
    return(
        <footer>
            <div className="container">
                <Nav nav={data.nav}/>
                <Links links={data.footer}/>
                <Icons icons={data.footer}/>
            </div>
        </footer>
    )
}