import React, {useEffect} from "react";
import './Users.sass'
import {User} from "./User/User";

export const Users = React.forwardRef((props, ref) => {





    return(
        <section className="users" ref={ref}>
            <div className="container">
                <h2 className={'text-center'}>{props.data.h2}</h2>
                <p className={'par1 text-center'}>{props.data.p}</p>
                <div className="row">
                    {props.users.map((user,i)=>(
                        <User
                            photo={user.photo}
                            name={user.name}
                            description={user.position}
                            email={user.email}
                            number={user.phone}
                            key={i}
                            int={i+1}
                            className={'user_'+ i}
                        />
                    ))}
                </div>
                {
                    props.vissible && <button
                        className={'button button_scnd'}
                        onClick = {props.onClick}
                    >Show more</button>
                }

            </div>
        </section>
    )
})