import React from "react";
import './Icons.sass'


export const Icons  = ({icons}) => {
    return(
        <div className="row icons">
            <div className="col-lg-3 col-md-4">
                <p>{icons[6]}</p>
            </div>
            <div className="offset-lg-5 col-lg-4 col-md-4 offset-md-4">
                {icons[5].map((icon, i)=>(
                    <div className={`icon icon_${ 1+ i}`} key={i}>
                        <img src={icon} alt=""/>
                    </div>
                ))}
            </div>
        </div>
    )
}