import React, {useEffect} from "react";
import './SideBar.sass'
import {NavLink} from "react-router-dom";


export const SideBar = React.forwardRef((props, ref) => {

    let [date, setDate] = React.useState('')
    useEffect(  () => {
        (async () => {
            let url = 'https://frontend-test-assignment-api.abz.agency/api/v1/users/1';
            let response = await fetch(url);
            let commits = await response.json();
            const {name, email, photo}  = commits.user
            setDate(date = {name,email,photo})
        })() }, [])






    return(
        <div className={`wrap_side_bar wrap_side_bar${ props.open ? '_open' : ''}`}>
            <div className="side_bar">
                <div className={'account'}>
                    <div className="account_data_wrap">
                        <p className={'account_name'}>{date.name}</p>
                        <p className={'account_email'}>{date.email}</p>
                    </div>
                    <img className="account_avatar" src={date.photo} alt="avatart" title={'avatar'}/>
                </div>
                <div className="links">
                    {props.data.map((link,i)=>(
                        <NavLink
                            ref = {ref}
                            className={'link_style_1 nav_element'}
                            to={'#'}
                            activeClassName={link.active}
                            key={i}
                            onClick = {()=>{
                                props.scrollTo(ref[i])
                                props.close()
                            }}
                        >
                            {link.name}
                        </NavLink>
                    ))}
                </div>
            </div>
        </div>
    )
})