import React from "react";
import {NavLink} from "react-router-dom";
import './Nav.sass'



export const Nav = React.forwardRef((props, ref) => {





    return(
        <>
            {props.data.map((link,i)=>(
                <NavLink
                    className={'link_style_1 nav_element'}
                    to={'#'}
                    activeClassName={link.active}
                    key={i}
                    onClick = {() => props.scrollTo(ref[i])}
                >
                    {link.name}
                </NavLink>
            ))}

        </>
    )
})