import React from "react";
import {NavLink} from "react-router-dom";
import './Nav.sass'

export const Nav = ({nav}) => {

    const ref = React.createRef()

    function click(){
        console.log(ref.current)
    }



    return(
        <div className="row footer_nav_wrap">
            <div className="col-lg-2 offset-lg-0 col-md-3 col-sm-6 offset-sm-3 offset-3 col-6">
                <img src={nav.logo} alt="logo" title={'logo'}/>
            </div>
            <div className="col-lg-7 offset-lg-3 col-md-8 offset-md-1">
                {nav.links.map((link,i)=>(
                    <NavLink
                        ref = {ref}
                        className={'link_style_1 nav_element'}
                        to={'#'}
                        key={i}
                        onClick = {()=>click()}
                    >
                        {link.name}
                    </NavLink>
                ))}
            </div>
        </div>
    )
}