import React from "react";
import './AboutMe.sass'


export const AboutMe = React.forwardRef((props, ref) => {


    return(
        <section className="about_me" ref={ref}>
            <div className="container">
                <div className="row">
                   <div className="col-lg-7">
                       <h1>{props.data.h1}</h1>
                       <p className={'par1'}>{props.data.p}</p>
                       <button
                           className={'button button_frst'}
                            onClick={()=>props.scroll(props.elemScroll)}
                       >
                           Sign Up
                       </button>
                   </div>
                </div>
            </div>
        </section>
    )
})