import React, {useEffect, useLayoutEffect} from "react";
import {Nav} from "./Nav/Nav";
import {Account} from "./Account/Account";
import './Header.sass'
import {SideBar} from "./SideBar/SideBar";




export const Header = React.forwardRef((props, ref) => {
    let [menuOpen, setMenuOpen] = React.useState(false)


    let Open = () => {
       setMenuOpen(!menuOpen)
    }

    function useWindowSize() {
        const [size, setSize] = React.useState([0]);
        useLayoutEffect(() => {
            function updateSize() {
                setSize([window.innerWidth]);
            }
            window.addEventListener('resize', updateSize);
            updateSize();
            return () => window.removeEventListener('resize', updateSize);
        }, []);

        return size;

    }


    return(
        <header>
            <div className="container">
                <div className="row">
                    <div className="col-lg-2 col-md-3 col-sm-6 col-6">
                        <div className="logo">
                            <img src={props.data.logo} alt="abz.agency" title={'abz.agency'}/>
                        </div>
                    </div>
                    {
                        useWindowSize() > 991 ?
                        <>
                        <div className="col-lg-7 nav">
                        <Nav
                            data={props.data.links}
                            scrollTo = {props.scrollTo}
                            ref={ref}
                        />
                        </div>
                        <div className="col-lg-3">

                        <Account user={props.data.user}/>

                        </div>
                        </> :
                            <>
                                <div className={'offset-md-8 col-md-1 offset-sm-3 col-sm-3 col-3 offset-3 button_open' }>
                                    <img
                                        src={props.data.menu}
                                        alt="menu"
                                        title={'open menu'}
                                        onClick={Open}
                                    />
                                </div>
                                <SideBar
                                    data={props.data.links}
                                    open = {menuOpen}
                                    close={Open}
                                    scrollTo = {props.scrollTo}
                                    ref={ref}
                                />
                            </>
                    }




                </div>
            </div>
        </header>
    )
})