import React, {useEffect} from "react";
import './Account.sass'



export const Account = () => {
    let [data, setData] = React.useState('')
    useEffect(  () => {
        (async () => {
            let url = 'https://frontend-test-assignment-api.abz.agency/api/v1/users/1';
            let response = await fetch(url);
            let commits = await response.json();
            const {name, email, photo}  = commits.user
            setData(data = {name,email,photo})
        })() }, [])



    return(
        <div className={'account'}>
            <div className="account_data_wrap">
                <p className={'account_name'}>{data.name}</p>
                <p className={'account_email'}>{data.email}</p>
            </div>
            <img className="account_avatar" src={data.photo} alt="avatart" title={'avatar'}/>
            <img className="account_sign_out" src="/img/icons/sign-out.svg" alt="sign out" title={'sign out'}/>
        </div>
    )
}
