import React from "react";
import './User.sass'

export const User = ({photo, name, description, email, number, className, int}) => {
    return (
        <div className={'col-lg-4 col-md-4'}>
           <div className={"user " + className}>
               <img src={photo} alt="User" title={'user '+ int}/>
               <div className="user_wrap">
                   <h3>
                       {name}
                       <p className={'tool_tip par3'}>{name}</p>
                   </h3>
                   <p className={'par3'}>{description}</p>
                   <p className={'par3'}>
                       {email}
                       <p className={'tool_tip par3'}>{email}</p>
                   </p>
                   <p className={'par3'}>{number}</p>


               </div>
           </div>
        </div>
    )
}