import React from "react";
import './Relationships.sass'
import {Lets_get_acquainted} from "./Let's get acquainted/Lets_get_acquainted";
import {About_my_relationships} from "./About my relationships/About_my_relationships";


export const Relationships = React.forwardRef((props, ref) => {

    return(
        <section className="relationships" ref={ref}>
            <div className="container text-left">
                <Lets_get_acquainted
                    data={props.data.lets_get_acquainted}
                    scroll={props.scroll}
                    elemScroll={props.elemScroll}
                />
                <About_my_relationships data={props.data.About_my_relationships}/>
            </div>
        </section>
    )
})