import React from "react";
import './Links.sass'



export const Links = ({links}) => {
    return(
        <div className="row contacts_wrap">
            <div className="col-lg-4 col-md-4">
                <ul className={'contacts'}>
                    {links[0].map((contact, i) => (
                        <li key={i} className={`contacts_element_${i + 1}`}>
                            <img src={contact.src} alt={'contact ' + i}/>
                            <p>{contact.li}</p>
                        </li>
                    ))}
                </ul>
            </div>
            <div className="col-lg-2 col-md-2">
                <ul className={'frst_links_footer'}>
                    {links[1].map((link, i)=>(
                        <li key={i} className={'frst_link_footer frst_link_footer' + i}>
                            <a href="#">{link}</a>
                        </li>))}
                </ul>
            </div>
            <div className="col-lg-2 col-md-2">
                <ul className={'scnd_links_footer'}>
                    {links[2].map((link, i)=>(
                        <li key={i} className={'frst_link_footer frst_link_footer' + i}>
                            <a href="#">{link}</a>
                        </li>))}
                </ul>
            </div>
            <div className="col-lg-2 col-md-2">
                <ul className={'thrd_links_footer'}>
                    {links[3].map((link, i)=>(
                        <li key={i} className={'frst_link_footer frst_link_footer' + i}>
                            <a href="#">{link}</a>
                        </li>))}
                </ul>
            </div>
            <div className="col-lg-2 col-md-2">
                <ul className={'frth_links_footer'}>
                    {links[4].map((link, i)=>(
                        <li key={i} className={'frst_link_footer frst_link_footer' + i}>
                            <a href="#">{link}</a>
                        </li>))}
                </ul>
            </div>
        </div>
    )
}